.card {
  width: 125px;
  height: 275px;
  margin: 10px;
  padding: 0.5em 1.5em;
  background: rgb(230, 230, 230);
  border-radius: 15px;
  box-shadow: 0px 1px 10px rgb(0, 0, 0);
}

.hiddenCard {
  width: 115px;
  height: 265px;
  margin: 10px;
  padding: 0.5em 1.5em;
  background-image: linear-gradient(176deg, #ffffff 8.33%, #ff0000 8.33%, #ff0000 50%, #ffffff 50%, #ffffff 58.33%, #ff0000 58.33%, #ff0000 100%);
  background-size: 60.00px 4.20px;
  border: 5px solid white;
  border-radius: 15px;
  box-shadow: 0px 1px 10px rgb(0, 0, 0);
}

.black {
  color: black;
}

.red {
  color: red;
}

.value {
  font-size: 400%;
  margin: 0;
}

.suit {
  font-size: 600%;
  margin: 0;
  text-align: center;
}

@media screen and (max-width: 992px) {
  .card {
    width: 60px;
    height: 180px;
  }

  .hiddenCard {
    width: 50px;
    height: 170px;
  }

  .value {
    font-size: 300%;
  }

  .suit {
    font-size: 500%;
  }
}

@media screen and (max-width: 600px) {
  .card {
    width: 30px;
    height: 90px;
    padding: 0.5em 1em;
  }

  .hiddenCard {
    width: 26px;
    height: 86px;
    padding: 0.5em 1em;
    border: 2px solid white;
  }

  .value {
    font-size: 150%;
  }

  .suit {
    font-size: 250%;
  }
}